.modal{
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 5;
    opacity: 0;
    pointer-events: none;
    transition: 500ms;
}

.modal.active{
    opacity: 1;
    transition: 500ms;
    pointer-events: all;
}

.modal-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    border-radius: 20px;
    background-color: #ffffff;
    height: 200px;
    width: 500px;
    transform: scale(0);
    transition: 1s;
}

.modal-content.active{
    transform: scale(1);
    transition: 1s;
}

.modal-user-container{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.modal-user{
    font-family: Montserrat, sans-serif;
    font-weight: 400;
    font-size: 30px;
}